import { contentClient, ChromeMessage } from '../chrome';

//  postMessage 
class ContentMsgClient {
    listenAndFrowardRequest() {
        console.log('content start listening message');
        return new Promise((resolve) => {
            window.addEventListener('message', async (e) => { 
                //  message 
                if (e.origin !== window.location.origin) { // 
                    return;
                }
                if (typeof e.data.method === 'undefined') {
                    return;
                }
                console.log('content start sending message to background :>>',e.data);
                const res = await contentClient.seedMessage(new ChromeMessage('contentRequest', e.data));
                // post msg back to proxyClient
                console.log('background start sending message to :>>',res);
                this.postMsg(res);
            });
        });
    }

    postMsg(message) {
        const targetOrigin = window.location.origin;
        const msg = JSON.parse(JSON.stringify(message));
        console.log("contetn post message :>>",msg,targetOrigin)
        window.postMessage(msg, targetOrigin);
    }
}

const contentMsgClient = new ContentMsgClient();

export {
    contentMsgClient
};
